.Burger{
    margin:auto;
    font-weight:bold;
    text-align:center;
    width:100%;
    height:250px;
    overflow:scroll;
    font-size:1.2 rem;
}

@media(min-width:700px) and (min-height:401)
{
    .Burger{
        width:600;
        height:400;

    }
}
@media(min-width:500) and (min-height:400)
{
    .Burger{
        width:350px;
        height:300px;
 
    }
}