.Toolbar{
    height:56px;
    width:100%;
    position:fixed;
    top:0;
    left:0;
    background-color:#703B09;
    display:flex;
    justify-content:space-between;
    align-items:center;
    padding:0 20px;
    box-sizing:border-box;
    z-index:90;
}

.Toolbar nav{
    height:100%;
}

.DrawerToggle {
    width: 40px;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: center;
    padding: 10px 0;
    box-sizing: border-box;
    cursor: pointer;
}

.DrawerToggle div {
    width: 90%;
    height: 3px;
    background-color: white;
}
.navdisplay{
    display:none;
}
.alignlogo{
    align-items: center;
    height:80%
}
@media (min-width: 500px) {
    .DrawerToggle {
        display: none;
    }
    .navdisplay{
        display:flex;
        flex-flow:column;
    }
    .alignlogo{
        align-items: right;
    }

    
}