.Input{
    box-sizing: border-box;
    width:100%;
    box-shadow:#eee;
    padding:10px 5px 10px 5px;
}
.Label{
   margin-bottom: 8px;
   font-weight: bold;
   display:block;
}
.inputElement{
    background-color: white;
    outline:none;
    padding:6px 8px;
    font:inherit;
    border: 1px solid #ccc;
}
.inputElement:focus{
    outline:none;
    background-color:#ccc;
}
.Invalid{
    background-color:salmon;
    border-color: red;
    
}